import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Tabs, Tab } from "@material-ui/core";
import FillerIcon from "@material-ui/icons/Language";

const tabItems = [
  { id: 1, title: "Web Design" },
  { id: 2, title: "Development" },
  { id: 3, title: "Website Updates" },
  { id: 4, title: "Web Design" },
];
const styles = makeStyles((theme) => ({
  root: {},
  tabContainer: {
    display: "flex",
    justifyContent: "center",
    position: "relative",
    top: "-20px",
  },
  tabRoot: {
    minHeight: "230px",
    backgroundColor: "rgba(30,25,55,1)",
    color: "rgb(184,179,208)",
    width: "300px",
    opacity: 1,
  },
  tabIcons: {
    fontSize: "60px",
    marginBottom: theme.spacing(2),
  },
  textWrapper: {
    textTransform: "none",
  },
}));
const WhatWeDoTabs = () => {
  const classes = styles();
  const [currentTab, setCurrentTab] = useState(1);
  const handleChange = (event, newValue) => {
    setCurrentTab(newValue);
  };

  return (
    <div className={classes.root}>
      <div className={classes.tabContainer}>
        <Tabs
          value={currentTab}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="auto"
          indicatorColor="primary"
        >
          {tabItems.map((item, index) => (
            <Tab
              key={index}
              label={item.title}
              icon={<FillerIcon className={classes.tabIcons} />}
              classes={{
                root: classes.tabRoot,
                wrapper: classes.textWrapper,
              }}
            />
          ))}
        </Tabs>
      </div>
    </div>
  );
};

export default WhatWeDoTabs;
