import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import home1 from "../../../media/home/home1.jpg";
const styles = makeStyles((theme) => ({
  root: {
    backgroundColor: "rgba(106, 110, 161,1)",
    height: "100vh",
    position: "relative",
  },
  introImg: {
    // backgroundPosition: "center center",
    // backgroundSize: "cover",
    // backgroundRepeat: "no-repeat",
    height: "100%",
    width: "100%",
    objectFit: "cover",
    opacity: 0.5,
  },
  infoContainer: {
    position: "absolute",
    top: "25%",
    left: 150,
    width: 500,
    "& span": {
      color: "rgb(106, 85, 139)",
      fontWeight: "800",
    },
    "& h2": {
      color: "white",
      fontSize: "3.25em",
      fontWeight: 700,
      margin: 0,
      lineHeight: "1.15em",
    },
    "& p": {
      fontSize: "1.25em",
      color: "white",
      wordWrap: "break-word",
    },
    [theme.breakpoints.down(650)]: {
      // paddingLeft: 16,
      left: "0",
      width: "100%",
      overflow: "none",
    },
  },
}));

const Intro = () => {
  const classes = styles();

  return (
    <div className={classes.root}>
      <img src={home1} alt="home1" className={classes.introImg} />
      {/* <div stlye={{ background: `url${home1}` }} className={classes.introImg} /> */}
      <div className={classes.infoContainer}>
        <span className={classes.info}>Welcome, i am person</span>
        <h2 className={classes.info}>I Design and build Websites</h2>
        <p className={classes.info}>
          Ea commodo laborum quis do esse velit dolore commodo quis velit.{" "}
        </p>
        <span style={{ color: "white" }}>person, personalized font</span>
      </div>
    </div>
  );
};

export default Intro;
