import React from "react";
import { Typography, Grid, CardActionArea } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const clients = [
  {
    id: 1,
    client_name: "wwf",
    client_logo:
      "https://image.shutterstock.com/image-photo/kiev-ukraine-may-13-2015nikon-260nw-279017711.jpg",
  },
  {
    id: 2,
    client_name: "cocacola",
    client_logo:
      "https://image.shutterstock.com/image-photo/kiev-ukraine-may-13-2015nikon-260nw-279017711.jpg",
  },
  {
    id: 3,
    client_name: "nike",
    client_logo:
      "https://image.shutterstock.com/image-photo/kiev-ukraine-may-13-2015nikon-260nw-279017711.jpg",
  },
  {
    id: 4,
    client_name: "starbucks",
    client_logo:
      "https://image.shutterstock.com/image-photo/kiev-ukraine-may-13-2015nikon-260nw-279017711.jpg",
  },
  {
    id: 5,
    client_name: "bently",
    client_logo:
      "https://image.shutterstock.com/image-photo/kiev-ukraine-may-13-2015nikon-260nw-279017711.jpg",
  },
  {
    id: 6,
    client_name: "nikon",
    client_logo:
      "https://image.shutterstock.com/image-photo/kiev-ukraine-may-13-2015nikon-260nw-279017711.jpg",
  },
];
const styles = makeStyles((theme) => ({
  root: {
    margin: theme.spacing(8, 0, 5, 0),
    padding: theme.spacing(7, 0, 10, 0),

    "& h3": {
      fontWeight: 700,
    },
    "& h6": {
      color: "rgb(106, 85, 139)",
    },
  },
  clientsContainer: {
    margin: theme.spacing(7, 0, 0, 0),
  },
  clientLogosContainer: {
    padding: theme.spacing(3),
  },
}));
const Clients = () => {
  const classes = styles();
  return (
    <div className={classes.root}>
      <Typography variant="h3" gutterBottom align="center">
        Clients
      </Typography>
      <Typography variant="subtitle1" gutterBottom align="center">
        Exercitation aute mollit ullamco proident.
      </Typography>

      <div className={classes.clientsContainer}>
        <Grid container>
          <Grid item xs={1} />
          <Grid item container xs={10}>
            {clients.map((item, index) => {
              return (
                <Grid key={index} item xs={12} sm={6} lg={4}>
                  <div className={classes.clientLogosContainer}>
                    <CardActionArea>
                      <img
                        src={item.client_logo}
                        alt={item.client_name}
                        width="100%"
                      />
                    </CardActionArea>
                  </div>
                </Grid>
              );
            })}
          </Grid>
          <Grid item xs={1} />
        </Grid>
      </div>
    </div>
  );
};

export default Clients;
