import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Grid, Typography, Button, CardActionArea } from "@material-ui/core";

const styles = makeStyles((theme) => ({
  root: {},
  imgContainer: {
    minHeight: "100vh",
    width: "100%",
    backgroundSize: "cover",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center center",
    background: `linear-gradient(rgba(106, 110, 161,.5), rgba(106, 110, 161,.5)), url(https://previews.123rf.com/images/antoniodiaz/antoniodiaz1506/antoniodiaz150600015/41597583-portrait-of-a-group-of-people-looking-at-a-laptop-computer-and-doing-some-work-in-an-office.jpg)`,
  },
  questionare: {
    opacity: 1,
    width: 480,
    margin: "0 auto",
    marginTop: theme.spacing(12),
    marginBottom: theme.spacing(8),
    color: "white",
    textAlign: "center",
    "& h3": {
      fontWeight: 700,
    },
    [theme.breakpoints.down("sm")]: {
      width: "100%",
    },
  },
  btn: {
    color: "white",
    backgroundColor: "rgba(120, 105, 195,1)",
    textAlign: "center",
    textTransform: "none",
  },
  mySitesContainer: {
    padding: theme.spacing(3),
  },
}));

const DesignedSitesSection = () => {
  const classes = styles();
  return (
    <div className={classes.root}>
      <div className={classes.imgContainer}>
        <div>
          <Grid container>
            <Grid item xs={1} />
            <Grid item container xs={10}>
              <Grid item xs={12}>
                <div className={classes.questionare}>
                  <Typography variant="h3" align="center" gutterBottom>
                    Want to see websites I have designed?
                  </Typography>
                  <Typography variant="h6" align="center" gutterBottom>
                    Laborum sint eu sunt aliqua magna sunt deserunt.
                  </Typography>
                  <Button className={classes.btn}>Portfolio</Button>
                </div>
              </Grid>
              {[0, 1].map((item, index) => (
                <Grid item xs={12} md={6} key={index}>
                  <div className={classes.mySitesContainer}>
                    <CardActionArea>
                      <img
                        src="https://restream.io/blog/content/images/2020/10/how-to-stream-live-video-on-your-website.png"
                        alt="mysite"
                        width="100%"
                        height="100%"
                      />
                    </CardActionArea>
                  </div>
                </Grid>
              ))}
            </Grid>
            <Grid item xs={1} />
          </Grid>
        </div>
      </div>
    </div>
  );
};

export default DesignedSitesSection;
