import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import CheckIcon from "@material-ui/icons/Check";

const styles = makeStyles((theme) => ({
  root: {
    margin: theme.spacing(10, 0, 5, 0),
  },
  infoContainer: {
    padding: theme.spacing(0, 5),
    "& h2": {
      fontWeight: 600,
    },

    [theme.breakpoints.down("sm")]: {
      paddingBottom: theme.spacing(6),
      "& h2": {
        fontSize: 32,
        fontWeight: 600,
      },
    },
  },
  imgContainer: {
    widht: "100%",
    "& img": {
      width: "100%",
    },
  },
}));
const Section1 = () => {
  const classes = styles();
  return (
    <div className={classes.root}>
      <Grid container>
        <Grid item xs={1} />
        <Grid item container xs={10} alignItems="center">
          <Grid item sm={12} md={6}>
            <div className={classes.infoContainer}>
              <Typography variant="h2" gutterBottom>
                This is for medium title with atleast 2 lines of text.
              </Typography>
              <Typography variant="body1" gutterBottom paragraph>
                Aliquip ea reprehenderit occaecat nulla consequat nisi nisi
                consectetur. Minim irure reprehenderit esse in non ex et.
                Eiusmod laboris qui quis adipisicing nulla reprehenderit
                incididunt anim. Ea sunt officia ea cupidatat irure Lorem dolor
                incididunt ut sit. Fugiat esse et nostrud amet sint.
              </Typography>
              <div>
                {[0, 1, 1].map((item, index) => {
                  return (
                    <div key={index}>
                      <span>
                        <CheckIcon
                          style={{
                            color: "rgb(124, 118, 155)",
                            fontSize: 32,
                            marginRight: 16,
                            fontWeight: 700,
                          }}
                        />
                      </span>
                      <span>
                        Ut voluptate est duis id dolor id ipsum enim duis.
                      </span>
                    </div>
                  );
                })}
              </div>
            </div>
          </Grid>
          <Grid item sm={12} md={6}>
            <div className={classes.imgContainer}>
              <img
                src="https://cdn.shopify.com/s/files/1/1180/9760/products/painting-portrait-size-24-x-36-1_grande.jpg?v=1490675490"
                alt="doggo"
              />
            </div>
          </Grid>
        </Grid>
        <Grid item xs={1} />
      </Grid>
    </div>
  );
};

export default Section1;
